.filter__list {
  flex-wrap: wrap;
  justify-content: center;
  padding: 10px;
  display: flex;
}

.filter__item {
  margin: 5px;
}

@media screen and (min-width: 640px) {
  .filter__item {
    margin: 10px;
  }
}

.filter__button {
  min-width: 100px;
  color: var(--color-light-gray);
  text-shadow: 0 -1px 2px #0003;
  cursor: pointer;
  white-space: nowrap;
  background: #6e7078 linear-gradient(#6e7078, #515156);
  border-radius: 8px;
  padding: 5px 10px;
  font-size: 10px;
  font-weight: 700;
  transition: all .2s ease-in-out;
  overflow: hidden;
  box-shadow: inset 0 1px #fff3, 0 3px 5px #00010680, 0 0 1px 1px #00010633;
}

@media screen and (min-width: 640px) {
  .filter__button {
    min-width: 120px;
    padding: 10px 15px;
    font-size: 12px;
  }
}

@media screen and (min-width: 1024px) {
  .filter__button {
    padding: 10px 20px;
    font-size: 14px;
  }
}

.filter__button:hover:not(:active) {
  background: #7e7e86 linear-gradient(#7e7e86, #46474c);
}

.filter__button.active {
  background: #4c4d52 linear-gradient(#4c4d52, #38393e);
  top: 1px;
  box-shadow: inset 0 0 1px #00000080, inset 0 2px 3px #00000080, 0 1px 1px #ffffff1a;
}

:root {
  --color-light-gray: #ccc;
  --color-gray: #6e6e6e;
  --color-dark-gray: #363636;
  --color-background: var(--color-light-gray);
  --color-text-main: var(--color-dark-gray);
  --color-text-secondary: var(--color-gray);
  --color-scrollbar: var(--color-dark-gray);
  --color-scrollbar-bg: var(--color-light-gray);
}

*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Inter, sans-serif;
  font-size: 16px;
}

* {
  scrollbar-color: var(--color-scrollbar) var(--color-scrollbar-bg);
  scrollbar-width: thin;
}

::-webkit-scrollbar {
  width: 9px;
  height: 8px;
}

::-webkit-scrollbar-thumb {
  background-color: var(--color-scrollbar);
}

::-webkit-scrollbar-track {
  background-color: var(--color-scrollbar-bg);
}

body {
  background-color: var(--color-background);
  background-image: url("bg.1a4dbeab.jpg");
  background-attachment: fixed;
  position: relative;
}

ul {
  list-style: none;
}

